.ChannelContainer {
  display: flex;
  position: relative;
  justify-content: center;
}

.PlaceholderPlayer {
  height: 400px;
  width: 400px;
  background-color: blue;
}

.ChannelGuide {
  display: flex;
  position: relative;
  overflow: auto;
  max-height: 90vh;
  flex-direction: column;
}

.ChannelPlaceholder {
  flex-shrink: 0;
  height: 200px;
  width: 200px;
  background-color: red;
  margin: 10px;
}
