.Container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.SearchBar {
  position: sticky;
  width: 100%;
  height: 4ex;
  padding: 5px;
  border-bottom: 1px solid grey;
}

.CategoryContainer {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
}

.CategoryList {
  display: flex;
  flex-grow: 1;
  flex-wrap: wrap;
}

.CategoryEntry {
  font-weight: bold;
  text-align: center;
  padding: 10px;
  margin: 8px;
  width: 5em;
  height: 1em;
  border: 1px solid black;
  border-radius: 1em;
  cursor: pointer;
}

.CategoryEntry--Selected {
  background-color: #3B7592;
}
